<template>
    <div class="bar">
        <span class="start">0%</span>
        <span class="end">100%</span>
        <div class="loaded" :style="calculateWidth()"></div>
    </div>
</template>

<script>
    export default {
        name: "Bar",
        props: ["question"],
        methods: {
            calculateWidth() {
                console.log(this.question / 10 * 100);
                console.log((this.question / 10 * 100 ) + '%');
                return 'width:'+Math.ceil(this.question / 11 * 100 ) + '%';
            }
        }
    }
</script>

<style lang="scss" scoped>
    .bar {
        width: 550px;
        display: flex;
        border: solid 1px;
        border-radius: 20px;
        font-size: 10px;
        line-height: 14px;
        padding: 3px 10px 2px;
        color: #2a2a2a;
        margin: -45px auto 0;
        position: relative;
        box-shadow: 2px 2px 4px rgba(10,20,30,.3);
        max-width: 90%;

        .start {
            margin-right: auto;
        }

        .end {
            margin-left: auto;
        }
    }

    .loaded {
        height: 20px;
        margin-top: -4px;
        margin-bottom: -4px;
        position: absolute;
        margin-left: -11px;
        border-radius: 30px 0 0 30px;
        z-index: 0;
        opacity: .4;
        background-color: #0a0a0a;
    }

</style>