<template>
    <div class="perguntas">
        <div class="pergunta" v-for="(pergunta, perguntaindex) in perguntas" :key="pergunta.id">

            <template v-if="currentPergunta == 0 && currentPergunta == pergunta.id ">
                <h3>Digite Seu E-mail</h3>
                <input type="text" class="email" name="email" v-model="pergunta.email">
                <span class="danger-note" v-if="!validMail && submittedMail">
                    Campo obrigatório, verifique se o seu e-mail é válido.
                </span>

                <h3>Digite Seu Nome</h3>
                <input type="text" class="email" name="name" v-model="pergunta.nome">
                <span class="danger-note" v-if="!validNome && submittedMail">
                    Campo obrigatório, por favor, digite seu nome.
                </span>
            </template>
            <template v-if="currentPergunta > 0 && currentPergunta == pergunta.id">
                <div v-if="pergunta.opcoes.tipo == 'checkbox'">
                    <div class="enunciado">
                        <span class="id">{{pergunta.id}}</span>
                        <span class="questao">
                            {{pergunta.questao}}
                            <br>
                    <div class="d-none">
                        {{currentPergunta}}
                        {{showMessage[currentPergunta]}}
                        {{upd}}
                    </div>
                    <span class="danger-note" v-if="showMessage[currentPergunta]">
                        Selecione a alternativa mais adequada.
                    </span>
                    </span>

                    </div>
                    <div class="respostas flex-desktop">

                        <div class="resposta" v-for="(item, index) in pergunta.opcoes.seleciona" :key="index"
                             @click.prevent="defineResposta(perguntaindex, index)">
                                <div class="selected"  v-if="index == pergunta.opcoes.selecionada" >
                                      <img src="Volante VG.png" alt="">
                                </div>

                                <div class="opened" v-else></div>
                            <!-- <img src="resposta escolhida.png" alt="" width="34px" height="34px"
                                 v-if="index == pergunta.opcoes.selecionada">
                            <img src="resposta.png" alt="" width="34px" height="34px" v-else> -->
                            <p> {{item}} </p>
                        </div>
                    </div>
                </div>
                <div v-if="pergunta.opcoes.tipo == 'grade'">
                    <div class="enunciado">
                        <span class="id">{{pergunta.id}}</span>
                        <span class="questao">
                            {{pergunta.questao}}
                            <div class="d-none">
                            {{currentPergunta}}
                            {{showMessage[currentPergunta]}}
                            {{upd}}
                        </div>
                        <span class="danger-note" v-if="showMessage[currentPergunta]">
                        Selecione a alternativa mais adequada.
                    </span>
                        </span>
                        <br>

                    </div>
                    <div class="flex padding-center">
                        <div class="op-start">
                            {{pergunta.opcoes.start}}
                        </div>

                        <div class="grades-possible">
                            <div class="resposta-linha text-center" v-for="item in pergunta.opcoes.numbers" :key="item"
                                 @click.prevent="defineResposta(perguntaindex, item)" >


                                <div class="selected" v-if="item == pergunta.opcoes.selecionada">
                                      <img src="Volante VG.png" alt="">
                                </div>

                                <div class="opened" v-else></div>
                                <!-- <img src="resposta escolhida.png" alt="" width="34px" height="34px"
                                     v-if="item == pergunta.opcoes.selecionada">
                                <img src="resposta.png" alt="" width="34px" height="34px" v-else> -->
                                <p> {{item}} </p>
                            </div>
                        </div>

                        <div class="op-end">
                            {{pergunta.opcoes.end}}
                        </div>
                    </div>
                </div>


                <div v-if="pergunta.opcoes.tipo == 'yesno'">
                    <div class="enunciado">
                        <span class="id">{{pergunta.id}}</span>
                        <span class="questao">
                            {{pergunta.questao}}
                            <div class="d-none">
                            {{currentPergunta}}
                            {{showMessage[currentPergunta]}}
                            {{upd}}
                        </div>
                        <span class="danger-note" v-if="showMessage[currentPergunta]">
                        Selecione a alternativa mais adequada.
                    </span>
                        </span>
                        <br>

                    </div>
                    <div class="flex padding-center">
                        <div class="op-start">
                            {{pergunta.opcoes.start}}
                            <div class="resposta-linha text-center"
                                 @click.prevent="defineResposta(perguntaindex, 0)">
                                <div class="selected" v-if="0 == pergunta.opcoes.selecionada">
                                      <img src="Volante VG.png" alt="">
                                </div>

                                <div class="opened" v-else></div>
                                <!-- <img src="resposta escolhida.png" alt="" width="34px" height="34px"
                                     v-if="0 == pergunta.opcoes.selecionada">
                                <img src="resposta.png" alt="" width="34px" height="34px" v-else> -->
                            </div>
                        </div>


                        <div class="op-end">
                            {{pergunta.opcoes.end}}
                            <div class="resposta-linha text-center"
                                 @click.prevent="defineResposta(perguntaindex, 2)">

                                <div class="selected" v-if="2 == pergunta.opcoes.selecionada">
                                    <img src="Volante VG.png" alt="">
                                </div>

                                <div class="opened" v-else></div>

                                <!-- <img src="resposta escolhida.png" alt="" width="34px" height="34px"
                                     v-if="2 == pergunta.opcoes.selecionada">
                                <img src="resposta.png" alt="" width="34px" height="34px" v-else> -->
                            </div>
                        </div>
                    </div>
                </div>
            </template>
        </div>

        <div class="container dis-flex">
            <a href="#" class="navigate" @click.prevent="changePergunta(currentPergunta-1)">
                <span>Anterior</span>
            </a>
            <img src="VSN logo horizontal.png" class="logo-middle" alt="">
            <a href="#" class="navigate navigate-2" @click.prevent="changePergunta(currentPergunta+1)">
                <span>Próxima</span>
            </a>
        </div>
        <div class="bar">
            <bar :question="currentPergunta"></bar>
        </div>
    </div>
</template>

<script>
    const $ = window.$;

    import Bar from './Bar';
    import {mask} from 'vue-the-mask'

    export default {
        directives: {mask},
        data() {
            return {
                sent: 0,
                currentPergunta: 0,
                nivelDeNeura: 0,
                upd: true,
                validMail: false,
                validNome: false,
                validBirth: false,
                submittedMail: false,
                showMessage: [false, false, false, false, false, false, false, false, false, false, false, false],
                perguntas: [
                    {
                        id: 0,
                        email: "",
                        nome: "",
                        birth: "",

                    },
                    {
                        id: 1,
                        questao: 'Qual a diferença de tempo entre você e os pilotos mais rápidos com mesmo tipo de carro/kart?',
                        opcoes: {
                            tipo: 'checkbox',
                            selecionada: null,
                            others: true,
                            seleciona: [
                                "Até 0,5 seg",
                                "Até 1,0 seg",
                                "Até 2,0 seg",
                                "Até 3,0 seg",
                                "Até 4,0 seg ou mais"
                            ]
                        }
                    },
                    {
                        id: 2,
                        questao: 'Consistência: qual é a variação média dos seus tempos de volta?',
                        opcoes: {
                            tipo: 'checkbox',
                            others: true,
                            selecionada: null,
                            seleciona: [
                                "Menos que 0,2 seg ",
                                "Menos que 0,5 seg",
                                "Até 1,0 seg",
                                "Mais que 1,5 seg",
                                "Mais que 2 seg"
                            ]
                        }
                    },
                    {
                        id: 3,
                        questao: 'No caso de perder o grip e ficar "de lado", você se sente no controle da situação?',
                        opcoes: {
                            tipo: 'grade',
                            others: false,
                            selecionada: null,
                            numbers: 5,
                            start: "Nem um pouco",
                            end: "Controle total"
                        }
                    },
                    {
                        id: 4,
                        questao: 'Se sente confortável pilotando na chuva?',
                        opcoes: {
                            tipo: 'grade',
                            others: false,
                            selecionada: null,
                            numbers: 5,
                            start: "Nunca nem andei",
                            end: "Bem tranquilo"
                        }
                    },
                    {
                        id: 5,
                        questao: 'Antes de entrar na pista, você tem na sua cabeça qual será o objetivo daquela saida?',
                        opcoes: {
                            tipo: 'grade',
                            others: false,
                            selecionada: null,
                            numbers: 5,
                            start: "Nunca",
                            end: "Sempre"
                        }
                    },
                    {
                        id: 6,
                        questao: 'Você analisa seus dados de câmera onboard ou telemetria após cada treino e corrida?',
                        opcoes: {
                            tipo: 'grade',
                            others: false,
                            selecionada: null,
                            numbers: 5,
                            start: "Nunca",
                            end: "Sempre"
                        }
                    },
                    {
                        id: 7,
                        questao: 'Qual é o seu nível de confiança ao buscar o limite do grip dos seus pneus?',
                        opcoes: {
                            tipo: 'grade',
                            others: false,
                            selecionada: null,
                            numbers: 5,
                            start: "Nenhuma",
                            end: "Confiança Total"
                        }
                    },
                    {
                        id: 8,
                        questao: 'Seus tempos de voltas são afetados ao andar atras de outro piloto?',
                        opcoes: {
                            tipo: 'grade',
                            others: false,
                            selecionada: null,
                            numbers: 5,
                            start: "Muito Afetados",
                            end: "Não mudam nada"
                        }
                    },
                                        {
                        id: 9,
                        questao: 'Se o seu carro estiver saindo de traseira somente nas entradas de curva, sabe o que e como pedir um acerto de setup?',
                        opcoes: {
                            tipo: 'grade',
                            others: false,
                            selecionada: null,
                            numbers: 5,
                            start: "Nem ideia",
                            end: "Sei exatamente"
                        }
                    },
                    {
                        id: 10,
                        questao: 'Você sabe qual a mudança de equilibrio é esperada ao aumentar a altura dianteira em 5mm?',
                        opcoes: {
                            tipo: 'yesno',
                            others: false,
                            selecionada: null,
                            numbers: 2,
                            start: "Não",
                            end: "Sim"
                        }
                    },
                ]
            }
        },
        components: {
            Bar
        },
        methods:
            {
                validateEmail() {
                    this.submittedMail = true;
                    if (this.perguntas[0].email == '' || !this.perguntas[0].email.includes("@"))
                        this.validMail = false;
                    else
                        this.validMail = true;

                    if (this.perguntas[0].nome == '')
                        this.validNome = false;
                    else
                        this.validNome = true;


                    this.firstSubmit();
                },

                firstSubmit(){
                    $('.type_EMAIL').val(this.perguntas[0].email);
                    $('.type_VC').val(this.perguntas[0].nome);
                    $('.type_DT').keyup();

                    setTimeout(function() {
                        $('.dinSubmit').click();
                        $('.formIntegration').submit();
                    }, 1000)

                },

                validateQuestions() {

                    // valida questões de 1 a 9
                    if (this.currentPergunta > 0 && this.currentPergunta <= 10) {
                        if (this.perguntas[this.currentPergunta].opcoes.selecionada != null) {
                            this.showMessage[this.currentPergunta] = false;
                            return true;
                        }
                        this.showMessage[this.currentPergunta] = true;
                        return false;
                    }

                    return true;
                },

                changePergunta(valor) {
                    console.log(valor);
                    console.log(this.validateQuestions())
                    this.validateEmail();
                    if (this.validMail && this.validNome && this.submittedMail && this.validateQuestions() || valor < this.currentPergunta) {
                        if (valor >= 1 && valor <= 10)
                            this.currentPergunta = valor;
                        
                        if(valor == -1)
                            console.log('unauthorized');
                    }

                    if(valor > 10) {
                        this.showNeura();
                    }

                },
                defineResposta(pergunta, resposta) {
                    this.perguntas[pergunta].opcoes.selecionada = resposta;
                },
                defineRespostaMultigrade(pergunta, resposta) {
                    this.perguntas[10].opcoes.answers[pergunta].grade = resposta;
                },
                calculateNeura() {
                    this.nivelDeNeura = 0

                    let sum = 0;


                    sum = (this.perguntas[1].opcoes.selecionada - 5) * -1;
                    sum = sum + (this.perguntas[2].opcoes.selecionada - 5) * -1;
                    sum = sum + this.perguntas[3].opcoes.selecionada;
                    sum = sum + this.perguntas[4].opcoes.selecionada;
                    sum = sum + this.perguntas[5].opcoes.selecionada;
                    sum = sum + this.perguntas[6].opcoes.selecionada;
                    sum = sum + this.perguntas[7].opcoes.selecionada;
                    sum = sum + this.perguntas[8].opcoes.selecionada;
                    sum = sum + this.perguntas[9].opcoes.selecionada;

                    // 47 ()
                    // 1 + 5 + 5 + 5 + 5 + 5 + 5 + 5 + 5 + 5

                    console.log(sum);
                    // media de 58,75 para cada resposta
                    if (sum < 12)
                        this.nivelDeNeura = 0;
                    else if (sum < 28)
                        this.nivelDeNeura = 1;
                    else if (sum < 32)
                        this.nivelDeNeura = 2;
                    else if(sum < 45)
                        this.nivelDeNeura = 3;
                    else {
                        this.nivelDeNeura = 4;
                    }

                    return this.nivelDeNeura;
                },
                showNeura() {
                    this.calculateNeura();
                    this.$emit('finished', this.nivelDeNeura);
                }
            },
        created() {
            setInterval(() => {
                this.upd = !this.upd;
            }, 500)
        }
    }
</script>

<style lang="scss">
    .perguntas {
        background-color: #d9d9d9;
        color: #3a3a3a;
        padding: 40px;
        margin-bottom: 0;
        margin-top: 0;
        

        .pergunta {
            width: 1080px;
            max-width: 90%;
            margin: auto;
            cursor: pointer;
            
            .flex-desktop {
                @media screen and (min-width: 1100px){
                    display: flex;

                    
                .resposta {
                    display: flex;
                    flex-grow: revert;
                    margin: 10px 0 15px 50px;

                    p {
                        margin: 0;
                        padding-left: 10px;
                        line-height: 35px;
                        width: fit-content;
                    }
                }
                }
            }

            .id {
                font-size: 46px;
                font-weight: bold;
                color: #a2b32d;
            }

            .questao {
                /*line-height: 16px;*/
                margin-top: 27px;
                margin-left: 10px;

            }

            .resposta {
                display: flex;
                flex-grow: revert;
                margin: 10px 0 15px 50px;

                p {
                    margin: 0;
                    padding-left: 10px;
                    line-height: 35px;
                    width: 150px;
                }

                img {
                    min-width: 34px;
                }
            }
        }

        .enunciado {
            display: flex;
            font-size: 16px;
        }
    }

    .dis-flex {
        display: flex;
        height: 200px;

        .navigate {
            width: 200px;
            display: inline-block;
            margin: auto auto auto 0;
            color: #2a2a2a;
            text-decoration: none;
            text-align: center;
            position: relative;
            border: solid 2px #2a2a2a;
            height: 40px;
            border-radius: 26px;
            transition: 300ms;

            &.navigate-2 {
                margin: auto 0 auto auto;
            }

            span {
                z-index: 2;
                color: #2a2a2a;
                display: inline-block;
                font-size: 20px;
                margin: 8px 0px;
                transition: 300ms;
            }

            &:hover {
                background-color: #3a3a3a;
                span {
                    color: #eaeaea;
                }
            }
        }

        .logo-middle {
            width: 200px;
            display: inline-block;
            height: auto;
            margin: 50px auto auto;
        }
    }

    .op-start {
        margin: auto;
        width: 200px;
        font-size: 16px;
        margin-top: 20px;
    }

    .op-end {
        margin: 20px auto;
        width: 250px;
        font-size: 16px;
    }

    .resposta-linha {
        margin: 10px auto;
        padding: 2px;
        text-align: center;

        p {
            margin: 0;
            padding: 0;
        }
    ;
    }

    .grades-possible {
        display: flex;
        margin: auto;
        width: 300px;
    }

    .padding-center {
        padding: 10px 100px;
        @media screen and (max-width: 500px){
            margin-left: -40px;
            margin-right: -40px;
            padding: 10px 0;
        }
        text-align: center;
        vertical-align: middle;
    }

    thead {
        th {
            text-align: center;
        }
    }

    tbody {
        td {
            text-align: center;
            padding: 10px 0 6px;
        }

        td:nth-child(1) {
            text-align: left;
        }

        tr:hover {
            background-color: #eaeaea;
            transition: 500ms;
        }
    }

    .email {
        width: 100%;
        height: 40px;
        line-height: 40px;
        border-radius: 8px;
        border: none;
        box-shadow: -3px -3px 5px rgba(255,255,255,.35), 1px 1px 5px rgba(10, 10, 10, .2);
        outline: none !important;
        font-size: 22px;
        padding-left: 20px;
        color: #717171;
        font-weight: 300;
    }

    .selected {
        border-radius: 6px;
        height: 34px;
        width: 34px;
        border: solid 2px #2a2a2a;
        background-color: #9a9a9a;
        display: block;
        margin: auto auto 10px auto;
        transition: 200ms;

        img {
            max-width: 75%;
            margin: 4px auto;
            display: block;
            min-width: 22px !important;
        }
    }

    .opened {
        border-radius: 6px;
        height: 34px;
        width: 34px;
        border: solid 2px #2a2a2a;
        display: block;
        margin: auto auto 10px auto;
    }

    .danger-note {
        font-size: 12px;
        color: #a2b32d;
    }

    .d-none {
        display: none;
    }


    @media screen and (max-width: 700px) {
        .dis-flex {
            display: flex;
            height: 200px;

            .navigate {
                width: 45%;
                display: inline-block;
                margin: auto auto auto 0;
                text-decoration: none;
                position: relative;

                &.navigate-2 {
                    margin: auto 0 auto auto;
                }

                img {
                    max-width: 100%;
                    position: absolute;
                    left: 0;
                    z-index: 1;
                    margin-top: 12px;
                    width: 130px !important;
                }

                span {
                    z-index: 2;
                    font-size: 18px;
                    opacity: .8;
                }
            }

            .logo-middle {
                width: 100px;
                display: inline-block;
                height: auto;
                margin: 50px auto auto;
                position: absolute;
                left: 50%;
                transform: translate(-50%, 0);
            }
        }

    }
</style>